.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.auth-box {
  background-color: var(--white);
  padding: 2.5rem;
  border-radius: 1rem;
  box-shadow: var(--shadow-lg);
  width: 100%;
  max-width: 400px;
  text-align: center;
}

.auth-box h2 {
  margin-bottom: 2rem;
  color: var(--primary-text);
  font-weight: 600;
  font-size: 1.75rem;
}

.input-group {
  position: relative;
  margin-bottom: 1.5rem;
}

.input-group .icon {
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--secondary-text);
}

.input-group input {
  width: 100%;
  padding: 1rem 1rem 1rem 3rem; /* Left padding for icon */
  border: 1px solid var(--border-color);
  border-radius: 0.5rem;
  background-color: var(--sidebar-bg);
  color: var(--primary-text);
  font-size: 1rem;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.input-group input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.2);
}

.auth-btn {
  width: 100%;
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
  background: linear-gradient(90deg, #ff8a65, #ffb74d);
  color: var(--white);
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 1rem;
  box-shadow: var(--shadow-sm);
}

.auth-btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.google-btn {
  width: 100%;
  padding: 1rem;
  border: 1px solid var(--border-color);
  border-radius: 0.5rem;
  background-color: var(--white);
  color: var(--primary-text);
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
  transition: background-color 0.2s;
  margin-bottom: 1.5rem;
}

.google-btn:hover {
  background-color: var(--hover-bg);
}

.google-btn svg {
  font-size: 1.25rem;
}

.toggle-auth {
  color: var(--secondary-text);
  font-size: 0.875rem;
}

.toggle-auth button {
  background: none;
  border: none;
  color: var(--primary);
  font-weight: 500;
  cursor: pointer;
  padding: 0 0 0 0.25rem;
}

.error-message {
  color: #ef4444; /* red-500 */
  font-size: 0.875rem;
  margin-bottom: 1rem;
}
