/* Main Layout */
.chat-container {
  display: flex;
  height: 100%;
  width: 100%;
  background-color: var(--white);
  border-radius: 1rem;
  box-shadow: var(--shadow-md);
  overflow: hidden;
}

/* Sidebar */
.sidebar {
  width: 320px;
  flex-shrink: 0;
  background-color: var(--sidebar-bg);
  border-right: 1px solid var(--border-color);
  display: flex;
  flex-direction: column;
}

.sidebar-header {
  padding: 1.5rem;
  border-bottom: 1px solid var(--border-color);
}

.sidebar-header h3 {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--primary-text);
}

.user-list {
  flex-grow: 1;
  overflow-y: auto;
  padding: 0.5rem;
}

.user-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem;
  border-radius: 0.5rem;
  text-decoration: none;
  color: var(--primary-text);
  transition: background-color 0.2s ease-in-out;
}

.user-item:hover {
  background-color: var(--hover-bg);
}

.user-item.active {
  background-color: var(--primary);
  color: var(--white);
}

.user-item.active .user-name,
.user-item.active .last-seen {
  color: var(--white);
}

.user-item.active .status-dot {
  border: 2px solid var(--sidebar-bg);
}

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: var(--primary);
  color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 0.875rem;
  flex-shrink: 0;
}

.user-info {
  flex-grow: 1;
  overflow: hidden;
  line-height: 1.4;
}

.user-name {
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
}

.last-seen {
  font-size: 0.75rem;
  color: var(--secondary-text);
}

.status-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 2px solid var(--sidebar-bg);
  transition: background-color 0.2s ease-in-out;
}

.status-dot.online {
  background-color: #22c55e; /* green-500 */
}

.status-dot.offline {
  background-color: #9ca3af; /* gray-400 */
}

.sidebar-footer {
  padding: 1rem 1.5rem;
  border-top: 1px solid var(--border-color);
  margin-bottom: 0.5rem; /* Margin for logout button */
}

.logout-btn {
  width: 100%;
  padding: 0.75rem;
  background: transparent;
  color: var(--secondary-text);
  border: 1px solid var(--border-color);
  border-radius: 0.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-weight: 500;
  transition: all 0.2s ease-in-out;
}

.logout-btn:hover {
  background: var(--hover-bg);
  color: var(--primary-text);
  border-color: #d1d5db; /* gray-300 */
}

/* Chat Window */
.chat-window {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  background-color: var(--white);
}

.chat-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  border-bottom: 1px solid var(--border-color);
  background-color: var(--white);
}

.chat-header .avatar {
  width: 48px;
  height: 48px;
  font-size: 1.25rem;
}

.chat-header h2 {
  font-size: 1.125rem;
  font-weight: 600;
  margin: 0;
}

.chat-header .status-text {
  font-size: 0.875rem;
  color: var(--secondary-text);
}

.messages-area {
  flex-grow: 1;
  padding: 1.5rem;
  overflow-y: auto;
  background-color: #f9fafb; /* gray-50 */
}

.message-form {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  border-top: 1px solid var(--border-color);
  background-color: var(--white);
}

.message-form input {
  flex-grow: 1;
  padding: 0.75rem 1rem;
  border: 1px solid var(--border-color);
  border-radius: 9999px;
  background-color: var(--sidebar-bg);
  color: var(--primary-text);
  font-size: 1rem;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.message-form input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.4);
}

.message-form button {
  flex-shrink: 0;
  width: 44px;
  height: 44px;
  border: none;
  border-radius: 50%;
  background-color: var(--primary);
  color: var(--white);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s ease-in-out;
  font-size: 1.25rem;
}

.message-form button:hover {
  background-color: var(--primary-hover);
}

.message-form button:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

/* Placeholder */
.placeholder {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: var(--secondary-text);
  text-align: center;
}

.placeholder svg {
  margin-bottom: 1rem;
}

.placeholder h3 {
  font-size: 1.25rem;
  font-weight: 500;
  color: var(--primary-text);
  margin-bottom: 0.5rem;
}

/* Reply Preview */
.reply-preview {
  padding: 0.75rem 1.5rem;
  background-color: var(--hover-bg);
  border-bottom: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.reply-preview-content {
  font-size: 0.875rem;
  overflow: hidden;
}

.reply-preview-sender {
  font-weight: 600;
  color: var(--primary-text);
}

.reply-preview-text {
  color: var(--secondary-text);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cancel-reply-btn {
  background: none;
  border: none;
  color: var(--secondary-text);
  font-size: 1.25rem;
  cursor: pointer;
  padding: 0.25rem;
  transition: color 0.2s;
}

.cancel-reply-btn:hover {
  color: var(--primary-text);
}

/* Responsive Design */
@media (max-width: 768px) {
  .sidebar {
    position: absolute;
    left: -100%;
    top: 0;
    bottom: 0;
    z-index: 10;
    transition: left 0.3s ease-in-out;
    width: 280px;
  }

  .sidebar.open {
    left: 0;
  }

  .chat-container {
    position: relative;
  }

  /* We would need a button to toggle the sidebar, which requires JS changes */
  /* For now, we'll just show the chat window takes full width */
  .chat-window {
    width: 100%;
  }
}
