/* Message Wrapper */
.message-wrapper {
  display: flex;
  flex-direction: column;
  gap: 0.25rem; /* 4px */
  margin-bottom: 1rem;
  max-width: 75%;
}

.message-wrapper.sent {
  align-self: flex-end;
  align-items: flex-end;
}

.message-wrapper.received {
  align-self: flex-start;
  align-items: flex-start;
}

/* Quoted Reply */
.quoted-reply {
  padding: 0.5rem 0.75rem;
  background-color: var(--hover-bg);
  border-radius: 0.75rem;
  border-bottom-left-radius: 0.25rem;
  border-left: 3px solid var(--primary);
  font-size: 0.875rem;
  max-width: 100%;
}

.message-wrapper.sent .quoted-reply {
  border-bottom-left-radius: 0.75rem;
  border-bottom-right-radius: 0.25rem;
}

.quoted-sender {
  font-weight: 600;
  color: var(--primary);
}

.quoted-text {
  color: var(--secondary-text);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Main Message Bubble */
.message-content {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.message-wrapper.sent .message-content {
  flex-direction: row-reverse;
}

.message {
  padding: 0.75rem 1rem;
  border-radius: 1.25rem;
  box-shadow: var(--shadow-sm);
  word-wrap: break-word;
  max-width: 100%;
}

.message-wrapper.sent .message {
  background-color: var(--sent-bg);
  color: var(--primary-text);
  border-bottom-right-radius: 0.25rem;
}

.message-wrapper.received .message {
  background-color: var(--received-bg);
  border: 1px solid var(--border-color);
  border-bottom-left-radius: 0.25rem;
}

.message-text {
  font-size: 1rem;
  line-height: 1.5;
}

/* Message Meta (Timestamp, Status, Edited) */
.message-meta {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-top: 0.25rem;
  font-size: 0.75rem;
  color: var(--secondary-text);
}

.edited-tag {
  font-style: italic;
}

.status-icon {
  font-size: 1.1em;
  color: var(--icon-color);
}

.status-icon.seen {
  color: var(--icon-color-seen);
}

/* Message Actions */
.message-actions-trigger {
  position: relative;
}

.reply-btn,
.more-actions-btn {
  background: none;
  border: none;
  color: var(--icon-color);
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.2s, background-color 0.2s, color 0.2s;
}

.message-wrapper:hover .reply-btn,
.message-wrapper:hover .more-actions-btn {
  visibility: visible;
  opacity: 1;
}

.reply-btn:hover,
.more-actions-btn:hover {
  background-color: var(--hover-bg);
  color: var(--primary-text);
}

.message-actions-menu {
  position: absolute;
  bottom: 100%;
  background: var(--white);
  border-radius: 0.5rem;
  box-shadow: var(--shadow-md);
  z-index: 10;
  width: max-content;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 0.25rem;
  margin-bottom: 0.25rem;
}

.message-wrapper.sent .message-actions-menu {
  right: 0;
}

.message-wrapper.received .message-actions-menu {
  left: 0;
}

.message-actions-menu button {
  background: none;
  border: none;
  color: var(--primary-text);
  padding: 0.5rem 0.75rem;
  text-align: left;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
  border-radius: 0.25rem;
  transition: background-color 0.2s;
}

.message-actions-menu button:hover {
  background-color: var(--hover-bg);
}

/* Edit View */
.message-edit-view {
  display: flex;
  gap: 0.5rem;
  width: 100%;
  align-items: center;
}

.message-edit-view input {
  flex-grow: 1;
  padding: 0.5rem 0.75rem;
  border: 1px solid var(--border-color);
  border-radius: 0.5rem;
  background: var(--white);
  color: var(--primary-text);
  font-size: 1rem;
}

.message-edit-view button {
  padding: 0.5rem 0.75rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.2s;
}

.message-edit-view button:first-of-type {
  background-color: var(--primary);
  color: var(--white);
}

.message-edit-view button:first-of-type:hover {
  background-color: var(--primary-hover);
}

.message-edit-view button:last-of-type {
  background-color: transparent;
  border: 1px solid var(--border-color);
  color: var(--primary-text);
}

.message-edit-view button:last-of-type:hover {
  background-color: var(--hover-bg);
}
